@import url(https://fonts.googleapis.com/css?family=Lato:400,900);
*,
*:before,
*:after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1rem;
  margin: 0;
  background: #fff;
  color: #222;
}

a {
  color: #ff9697;
  font-weight: 400;
  letter-spacing: 1px;
  text-decoration: none;
  background: #fff;
  padding: 20px 5px;
  display: inline-block;
  width: 100%;
  text-align: center;
  transition: all 0.5s;
}

a:hover,
a:focus {
  text-decoration: underline;
}

.toggleNav {
  display: none;
}

img {
  width: 100%;
  cursor: pointer;
  transition: opacity 0.3s ease-out;
}

img:hover {
  opacity: 0.5;
}

.wrapper {
  max-width: 1000px;
  margin: 0 auto;
}

.top h1 a {
  font-size: 2rem;
  font-weight: 900;
}

h2 {
  font-size: 1.75rem;
}

input {
  padding: 10px;
  border: 0;
}

section,
footer {
  text-align: center;
  background: #fff;
  padding: 20px;
  margin: 20px 0;
  font-weight: 100;
}

form {
  padding: 20px;
  margin: 20px 0;
}

footer {
  border-top: 1px solid #e2e2e2;
}

.flex-nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.flex-nav li {
  flex: 3 1;
}

.flex-nav li a {
  color: #8b8b8b;
}

.flex-nav li a:hover,
.flex-nav li a:focus {
  color: #222;
}

.flex-nav .social {
  flex: 1 1;
}

.gallery ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
}

.gallery li {
  flex: 1 1 50%;
  padding: 12px;
}

@media all and (max-width: 768px) {
  .flex-nav li,
  .gallery li {
    flex-basis: 100%;
  }

  .wrapper {
    display: flex;
    flex-direction: column;
  }

  .wrapper > * {
    order: 99;
  }

  .flex-nav {
    order: 1;
  }

  .toggleNav {
    display: block;
    color: #8b8b8b;
  }

  .toggleNav:hover,
  .toggleNav:focus {
    color: #222;
    text-decoration: none;
  }

  .flex-nav {
    border-bottom: 1px solid #e2e2e2;
  }

  .flex-nav ul {
    display: none;
    flex-wrap: wrap;
  }

  .flex-nav ul.open {
    display: flex;
  }

  .top {
    order: 2;
  }

  .details {
    order: 3;
  }
}

